<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="bono.meta_semana"
    :progress="bono.progress"
    :name="bono.nombre"
    :depto="bono.depto"
    :city="bono.city"
    :days="bono.dias_laborados"
    :m3="bono.m3_persona"
    :bonus_p="bono.bono_persona"
    :bonus_tot="bono.bono_depto"
    :asis="bono.asistencia"
    :extra="bono.$_extra_m3"
    :bonus_prod="bono.bono_productividad"
    :bonus_goals="bono.bono_metas"
    :base="bono.pago_base"
    />
</div>
</template>

<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Bono',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'bonogarantia',
            userCode: this.$route.params.user,
            city: 'tijuana'
        }); 
    },
    computed: {
        isLoading() {            
            return this.$store.getters["plants/loading"];
        },
        bono() {            
            return this.$store.getters["plants/data"];
        }
    }
}
</script>